import React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import "../styles/main-contact-us.css"

export default function ContactUs() {
  const description = `中小企業のIT相談ならホットソウルにお任せください！人生を豊かにするITの実現を目指し、世界に開かれた視野と、自らの足元を大切に、地道に活動する会社です。| 中小企業のデジタル化・IT活用サポート HOTSOUL`
  const pageUrl = `https://www.hotsoul.jp/contact-us/`

  return (
    <Layout>
      <Seo title="お問い合わせ" description={description} pageUrl={pageUrl} />
      <section className="wrap contact">
        <h2>お問い合わせ</h2>
        <div className="contact-inner">
          <iframe
            title="入力フォーム"
            src="https://docs.google.com/forms/d/e/1FAIpQLSePGpIw5XyyWUlc-IFMKoHzF9BvMCoKgv2IYtbG36U-xaSwwA/viewform?embedded=true"
            frameBorder="0"
            width="920"
            height="1550"
            marginHeight="0"
            marginWidth="0"
          >
            読み込んでいます…
          </iframe>
        </div>
      </section>
    </Layout>
  )
}